.dropdown-navigation {
  padding: 0;
  list-style: none; }
  .dropdown-navigation__item {
    margin-bottom: 16px; }
    @media screen and (min-width: 1120px) {
      .dropdown-navigation__item {
        margin-bottom: 18px; } }
  .dropdown-navigation__link {
    display: block;
    padding-left: 10px;
    font-family: "TT Lakes", "Verdana", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-decoration: none;
    color: #000;
    transition: all 0.2s ease-in;
    border: 4px solid transparent; }
    @media screen and (min-width: 1120px) {
      .dropdown-navigation__link {
        padding-top: 2px;
        padding-bottom: 2px;
        border-width: 0;
        border-left-width: 4px; } }
    .dropdown-navigation__link--active {
      color: #7E7E82;
      border-left-color: #41F6D7; }
    .dropdown-navigation__link:hover {
      color: #98989b; }
