.page-main {
  display: block;
  padding-top: 1px;
  padding-bottom: 40px;
  background-color: #F8F8F8; }
  @media screen and (min-width: 1120px) {
    .page-main {
      margin-left: 308px; } }
  .page-main__header {
    padding-top: 20px;
    margin-bottom: 20px; }
    @media screen and (min-width: 426px) {
      .page-main__header {
        margin-bottom: 32px; } }
    @media screen and (min-width: 1120px) {
      .page-main__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 32px;
        padding-top: 42px;
        margin-bottom: 8px; } }
  .page-main__main-title {
    padding-left: 12px;
    margin: 0;
    font-size: 28px;
    line-height: 40px;
    font-weight: 700;
    letter-spacing: -0.6px;
    border: 4px solid transparent;
    border-left: 4px solid #41F6D7; }
    @media screen and (min-width: 426px) {
      .page-main__main-title {
        padding-left: 20px; } }
    @media screen and (min-width: 1120px) {
      .page-main__main-title {
        padding-left: 30px;
        border-width: 0;
        border-left-width: 3px; } }
  .page-main__navigation {
    margin-top: 0;
    margin-left: 16px;
    margin-bottom: 24px; }
    @media screen and (min-width: 426px) {
      .page-main__navigation {
        margin-left: 24px;
        margin-bottom: 32px; } }
  .page-main__about-us {
    margin-bottom: 24px; }
    @media screen and (min-width: 620px) {
      .page-main__about-us {
        margin-bottom: 32px; } }
  .page-main__user-menu {
    display: none;
    margin: 0; }
    @media screen and (min-width: 1120px) {
      .page-main__user-menu {
        display: flex; }
        .page-main__user-menu .user-menu__link {
          font-size: 16px; }
          .page-main__user-menu .user-menu__link::after {
            margin-left: 8px; } }
