.burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  padding-left: 10px;
  background-color: #FF3E79;
  border: none;
  border-radius: 50%; }
  .burger::before {
    content: '';
    display: block;
    width: 18px;
    height: 2px;
    background-color: #fff; }
  .burger::after {
    content: '';
    display: block;
    width: 14px;
    height: 2px;
    background-color: #fff; }
  .burger__middle-line {
    width: 10px;
    height: 2px;
    margin-top: 5px;
    margin-bottom: 5px;
    background-color: #fff; }
