.brands {
  border-top: 1px solid #D9FFF5;
  margin-bottom: 24px;
  overflow: hidden; }
  @media screen and (min-width: 426px) {
    .brands {
      margin-bottom: 32px; } }
  .brands__title {
    padding: 16px;
    margin-top: 0;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: -0.2px;
    color: #7E7E82;
    background-image: linear-gradient(to right, #f8f8f8, #ffffff); }
    @media screen and (min-width: 426px) {
      .brands__title {
        padding-left: 24px;
        padding-right: 24px;
        margin-bottom: 24px; } }
    @media screen and (min-width: 1120px) {
      .brands__title {
        padding-left: 32px;
        padding-right: 32px; } }
  .brands__list {
    display: flex;
    padding-left: 16px;
    list-style: none; }
    @media screen and (min-width: 426px) {
      .brands__list {
        flex-wrap: wrap;
        justify-content: left;
        height: 176px;
        margin-bottom: 8px;
        padding-left: 24px;
        overflow: hidden; } }
    @media screen and (min-width: 1120px) {
      .brands__list {
        padding-left: 32px; } }
    .brands__list--expand {
      height: auto; }
  .brands__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 240px;
    height: 72px;
    padding-left: 16px;
    padding-right: 16px;
    margin-right: 16px;
    flex-shrink: 0;
    background-color: #fff;
    border: 1px solid #EAEAEA;
    border-radius: 6px; }
    @media screen and (min-width: 426px) {
      .brands__item {
        width: 224px;
        margin-bottom: 16px;
        margin-right: 24px; } }
    @media screen and (min-width: 1120px) {
      .brands__item {
        margin-right: 32px; } }
  .brands__arrows-link {
    display: none; }
    @media screen and (min-width: 426px) {
      .brands__arrows-link {
        display: inline-flex;
        margin-left: 24px; } }
    @media screen and (min-width: 1120px) {
      .brands__arrows-link {
        margin-left: 32px; } }
  .brands__image {
    flex-shrink: 0; }
