@media screen and (min-width: 620px) {
  .about-us {
    display: flex;
    margin-left: 24px;
    margin-right: 24px; } }

@media screen and (min-width: 1120px) {
  .about-us {
    margin-left: 31px;
    margin-right: 30px; } }

.about-us__text-content {
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 16px; }
  @media screen and (min-width: 620px) {
    .about-us__text-content {
      margin-left: 0; } }

.about-us__text {
  height: 90px;
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  overflow: hidden; }
  @media screen and (min-width: 620px) {
    .about-us__text {
      height: 160px;
      line-height: 20px; } }
  .about-us__text:last-child {
    margin-bottom: 10px; }
  .about-us__text--expand {
    height: auto; }

.about-us img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  flex-shrink: 0; }
  @media screen and (min-width: 620px) {
    .about-us img {
      align-self: flex-start;
      width: 50%;
      margin-left: 48px; } }
  @media screen and (min-width: 1120px) {
    .about-us img {
      margin-left: 30px; } }
