.tech-list {
  display: flex;
  padding-left: 16px;
  padding-right: 16px;
  list-style: none; }
  @media screen and (min-width: 426px) {
    .tech-list {
      flex-wrap: wrap;
      justify-content: left;
      height: 176px;
      padding-left: 24px;
      padding-right: 0;
      overflow: hidden; } }
  @media screen and (min-width: 1120px) {
    .tech-list {
      padding-left: 32px; } }
  .tech-list--expand {
    height: auto; }
  .tech-list__item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-shrink: 0;
    padding: 16px;
    margin-right: 16px;
    width: 240px;
    min-height: 160px;
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 6px; }
    @media screen and (min-width: 426px) {
      .tech-list__item {
        width: 224px;
        margin-right: 24px;
        margin-bottom: 16px;
        flex-shrink: 1; } }
    @media screen and (min-width: 1120px) {
      .tech-list__item {
        width: 240px;
        margin-right: 32px; } }
  .tech-list__title {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px; }

.tech {
  margin-bottom: 24px;
  border-top: 1px solid #D9FFF5;
  overflow: hidden; }
  .tech__title {
    padding: 16px;
    margin: 0 0 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: -0.2px;
    color: #7E7E82;
    background-image: linear-gradient(to right, #f8f8f8, #ffffff); }
    @media screen and (min-width: 426px) {
      .tech__title {
        padding-left: 24px;
        padding-right: 24px;
        margin-bottom: 24px; } }
    @media screen and (min-width: 1120px) {
      .tech__title {
        padding-left: 32px;
        padding-right: 32px; } }
  @media screen and (min-width: 426px) {
    .tech__list {
      margin-bottom: 0; } }
  .tech__link {
    align-self: flex-end; }
  .tech__arrows-link {
    display: none; }
    @media screen and (min-width: 426px) {
      .tech__arrows-link {
        display: inline-flex;
        margin-left: 24px; } }
    @media screen and (min-width: 1120px) {
      .tech__arrows-link {
        margin-left: 32px; } }
