.price-policy {
  padding-left: 16px;
  padding-right: 16px; }
  @media screen and (min-width: 426px) {
    .price-policy {
      max-width: 469px;
      padding-left: 24px;
      padding-right: 24px; } }
  .price-policy__text {
    margin-top: 0;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px; }
  .price-policy__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-decoration: none;
    color: #000; }
    .price-policy__link::after {
      content: '';
      display: inline-block;
      width: 8px;
      height: 8px;
      margin-left: 10px;
      border: 2px solid #FF3E79;
      border-left: transparent;
      border-bottom: transparent;
      transform: rotate(45deg); }
