*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

html,
body {
  margin: 0;
  padding: 0;
  font-family: "TT Lakes", "Verdana", sans-serif;
  color: #000; }

img {
  max-width: 100%; }

.headline-hidden {
  font-size: 0; }

.fixed {
  overflow: hidden; }

.logo {
  min-width: 75px; }

.page {
  max-width: 1440px;
  margin: auto; }

.pagination {
  display: flex;
  justify-content: center; }

.prices__t-body, .tech__list, .brands__list {
  transition-duration: 0.5s !important; }

.swiper-pagination-bullet {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  opacity: 1;
  margin: 0 14px 0 0 !important;
  margin: 0 14px 0 0 !important;
  background-color: #ddd; }
  .swiper-pagination-bullet:last-child {
    margin: 0 !important; }
  .swiper-pagination-bullet-active {
    background-color: #b5b6bc; }

.primary-btn {
  display: block;
  font-size: 0;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer; }
  .primary-btn:hover::after {
    background-color: #ff8bae; }
  .primary-btn::after {
    content: "";
    display: block;
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    background-color: #FF3E79;
    background-repeat: no-repeat;
    background-position: center center;
    border-radius: 50%; }
  .primary-btn--type_call::after {
    background-image: url(./static/phone.svg); }
  .primary-btn--type_mail::after {
    background-image: url(./static/message.svg); }
  .primary-btn--type_profile::after {
    background-image: url(./static/profile.svg); }
  .primary-btn--type_order::after {
    background-image: url(./static/order.svg); }
  .primary-btn--type_status::after {
    background-image: url(./static/status.svg); }
  .primary-btn--type_search::after {
    background-image: url(./static/search.svg); }
  .primary-btn--type_close::after {
    background-image: url(./static/close.svg); }

.order-btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 120px;
  max-width: 150px;
  padding: 4px 20px;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background-color: #FF3E79;
  border: none;
  border-radius: 16px;
  transition: box-shadow 500ms; }
  .order-btn:hover {
    box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.8);
    color: black;
    transition: box-shadow, color 0.3s; }
  .order-btn:active {
    box-shadow: inset 2px 2px 0 0 rgba(0, 0, 0, 0.8); }
  .order-btn::after {
    content: "";
    display: inline-block;
    width: 8px;
    height: 8px;
    margin-left: 10px;
    border: 2px solid #fff;
    border-left: transparent;
    border-bottom: transparent;
    transform: rotate(45deg); }

.arrow-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  font-size: 0;
  border: 2px solid #FF3E79;
  border-radius: 50%; }
  .arrow-btn:hover {
    background-color: #FF3E79; }
    .arrow-btn:hover::before {
      border-right-color: #fff;
      border-top-color: #fff; }
  .arrow-btn::before {
    content: "";
    display: block;
    margin-left: -4px;
    width: 8px;
    height: 8px;
    border: 2px solid #FF3E79;
    border-left: transparent;
    border-bottom: transparent;
    transform: rotate(45deg); }

.arrows-link {
  display: inline-flex;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.2px;
  text-decoration: none;
  color: #000; }
  .arrows-link::before {
    content: "";
    display: inline-block;
    width: 24px;
    height: 24px;
    margin-right: 8px;
    background-image: url(./static/arrows.png); }
  .arrows-link--reverse::before {
    transform: rotate(180deg); }

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.96); }
