.page-header {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 19px 16px;
  box-shadow: inset 0 -1px 0 #D9FFF5; }
  @media screen and (min-width: 540px) {
    .page-header {
      padding: 8px 24px; } }
  @media screen and (min-width: 1120px) {
    .page-header {
      display: none; } }
  .page-header__burger {
    margin-right: 16px; }
    @media screen and (min-width: 430px) {
      .page-header__burger {
        margin-right: 20px; } }
  .page-header__logo {
    display: flex;
    align-items: center;
    margin-right: auto; }
    @media screen and (min-width: 430px) {
      .page-header__logo::before {
        content: "";
        display: block;
        width: 2px;
        height: 32px;
        margin-right: 20px;
        background-color: #EAEAEA; } }
  .page-header__socials {
    display: none;
    margin-left: 20px; }
    @media screen and (min-width: 540px) {
      .page-header__socials {
        display: flex;
        justify-content: flex-end; } }
  .page-header__user-menu {
    margin: 0;
    margin-left: 16px; }
    .page-header__user-menu::before {
      content: "";
      display: block;
      width: 2px;
      height: 32px;
      background-color: #EAEAEA; }
    @media screen and (min-width: 426px) {
      .page-header__user-menu {
        margin-left: 4px; } }
