.dropdown-menu {
  position: fixed;
  top: 0;
  left: -500px;
  z-index: 100;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  padding-bottom: 30px;
  background-color: #fff;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
  transition: left 0.4s ease-out; }
  @media screen and (min-width: 426px) {
    .dropdown-menu {
      width: 320px; } }
  @media screen and (min-width: 1120px) {
    .dropdown-menu {
      left: auto;
      width: 306px;
      padding-left: 1px;
      box-shadow: none; } }
  @media screen and (max-height: 720px) {
    .dropdown-menu {
      overflow: scroll; } }
  .dropdown-menu--open {
    left: 0; }
  .dropdown-menu__header {
    display: flex;
    align-items: center;
    padding: 16px; }
  .dropdown-menu__close {
    margin-right: 16px; }
    @media screen and (min-width: 1120px) {
      .dropdown-menu__close {
        display: none; } }
  .dropdown-menu__logo {
    margin-right: auto; }
  .dropdown-menu__search {
    width: 40px;
    height: 40px;
    font-size: 0;
    background-color: #FF3E79;
    background-image: url(./static/search.svg);
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
    border-radius: 50%; }
  .dropdown-menu__navigation {
    margin-bottom: auto; }
  .dropdown-menu__contacts {
    margin-top: 30px; }
