.feedback-modal-wrap {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 500;
  display: none;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.96); }
  .feedback-modal-wrap--open {
    display: block; }

.feedback-modal {
  position: fixed;
  top: 0;
  right: -600px;
  z-index: 200;
  width: 100vw;
  height: 100vh;
  padding-top: 100px;
  background-color: #fff;
  transition: right 300ms; }
  @media screen and (min-width: 426px) {
    .feedback-modal {
      width: 320px;
      box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2) -2px 0px 4px rgba(69, 79, 126, 0.02); } }
  @media screen and (min-width: 1120px) {
    .feedback-modal {
      width: 440px;
      padding-top: 32px; } }
  .feedback-modal--open {
    right: 0; }
  .feedback-modal__close {
    position: absolute;
    top: 24px;
    left: 16px; }
    @media screen and (min-width: 1120px) {
      .feedback-modal__close {
        top: 32px;
        left: -72px; } }
  .feedback-modal__title {
    margin-top: 0;
    margin-bottom: 24px;
    padding-left: 12px;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    border-left: 3px solid #41F6D7; }
    @media screen and (min-width: 1120px) {
      .feedback-modal__title {
        padding-left: 30px; } }

.feedback-form {
  margin-left: 16px;
  margin-right: 16px; }
  @media screen and (min-width: 1120px) {
    .feedback-form {
      margin-left: 32px;
      margin-right: 32px; } }
  .feedback-form textarea,
  .feedback-form input {
    width: 100%;
    padding: 12px 16px;
    margin-bottom: 16px;
    font-family: "TT Lakes", "Verdana", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #000;
    border: 2px solid #EAEAEA;
    border-radius: 8px; }
    .feedback-form textarea:focus,
    .feedback-form input:focus {
      outline: none;
      border-color: #41F6D7; }
  .feedback-form textarea::-moz-placeholder,
  .feedback-form input::-moz-placeholder {
    font-family: "TT Lakes", "Verdana", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #B5B6BC; }
  .feedback-form textarea::placeholder,
  .feedback-form input::placeholder {
    font-family: "TT Lakes", "Verdana", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #B5B6BC; }
  .feedback-form__button {
    margin-left: auto; }
  .feedback-form__message {
    resize: unset; }
  .feedback-form__text {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82; }
  .feedback-form a {
    text-decoration: none;
    color: #FF3E79; }
